
.card-question {
  position: relative;
  width: calc(100% - 12px);
  height: calc(100% - 12px);  
  background: linear-gradient(155.61deg, #29CC91 19.6%, rgba(41, 204, 145, 0.37) 105.44%, rgba(41, 204, 145, 0) 105.45%);
  padding: 6px;
  border-radius: 10px;
  touch-action: pan-x;
  will-change: transform, scale;
  overflow: hidden;
}

.card-question__choose-blur {
  position: absolute;
  pointer-events: none;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  transition: background-color 0.5s ease;
  color: #000;
  font-size: 10rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-question__inner {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  border-radius: 10px;
  padding: 30px;
  border: 1px solid rgba(255,255,255,.3);
  box-sizing: border-box;
}

.card-question__text {
  font-family: "Roboto", sans-serif;
  font-size: 25px;
  line-height: 40px;
  text-align: center;
  letter-spacing: 0.2px;
  color: #fff;
  text-align: center;
}

.card-question__text_middle {
  position: relative;
  display: flex;
  align-items: center;
}

.card-question__text_middle:after,
.card-question__text_middle:before {
  content: "";
  width: 115px;
  height: 1px;
  background: #EBEBEB;
  opacity: .3;
}

.card-question__text_middle:after {
  margin-left: 9px;
}

.card-question__text_middle:before {
  margin-right: 9px;
}