.statistic {
  padding: 8px 16px;
  font-family: "Roboto", sans-serif;
}

.statistic__inner {
  padding-top: 16px;
}

.statistic__title {
  text-align: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: #000;
  margin-bottom: 8px;
}

.statistic__subtitle {
  text-align: center;
  letter-spacing: 0.2px;
  font-size: 14px;
  line-height: 18px;
  color: #6d7885;
  margin-bottom: 72px;
}

.statistic__yes,
.statistic__no {
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.1px;
  color: #67BB49;
}

.statistic__not {
  color: #CE7979;
}

.statistic__content {
  margin-bottom: 44px;
}

.statistic__icon {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}